import type { Flash } from '#/schema/flash'
import type { Remix } from '@remix-run/dom'

type Variant = 'default' | Flash['type']

interface AlertProps extends Remix.Props<'div'> {
  variant?: Variant
}

const alertStyles = {
  default: {
    backgroundColor: 'var(--muted)',
    color: 'var(--foreground)',
    borderColor: 'var(--border)',
  },
  success: {
    backgroundColor: 'var(--success-background)',
    color: 'var(--success)',
    borderColor: 'var(--success)',
  },
  destructive: {
    backgroundColor: 'var(--destructive-background)',
    color: 'var(--destructive)',
    borderColor: 'var(--destructive)',
  },
  warning: {
    backgroundColor: 'var(--warning-background)',
    color: 'var(--warning)',
    borderColor: 'var(--warning)',
  },
}

const baseStyles = {
  padding: 'var(--spacing-4)',
  borderRadius: 'var(--rounded)',
  border: '1px solid',
  fontSize: 'var(--text-sm)',
  display: 'flex',
  gap: 'var(--spacing-3)',
  alignItems: 'flex-start',
}

export function Alert({ variant = 'default', css, children, ...rest }: AlertProps) {
  return (
    <div
      role="alert"
      {...rest}
      css={{
        ...baseStyles,
        ...alertStyles[variant],
        ...css,
      }}
    >
      {children}
    </div>
  )
}

export function AlertTitle({ css, ...rest }: Remix.Props<'h5'>) {
  return (
    <h5
      {...rest}
      css={{
        fontWeight: 'var(--font-semibold)',
        fontSize: 'var(--text-sm)',
        marginBottom: 'var(--spacing-1)',
        ...css,
      }}
    />
  )
}

export function AlertDescription({ css, ...rest }: Remix.Props<'div'>) {
  return (
    <div
      {...rest}
      css={{
        fontSize: 'var(--text-sm)',
        opacity: 0.9,
        ...css,
      }}
    />
  )
}
